            <div v-for="values in items" :key="values.news_id" >
              <div class="m-2.5">
                <div class="w-full"><img :src="`${values.news_image[0].url}`" class="w-full h-28" alt=""></div>
                <div style="width: 100%;max-height: 3rem"><p id="text" class="font-bold text-black text-sm m-1">{{values.title}}</p></div>
                <div class="flex flex-row align-center w-full justify-start" >
                  <div><span class="font-semibold text-slate-800 text-start" :style="{fontSize:size+'px'}">{{values.visit}}نمایش</span></div>
                  <div><span class="font-semibold text-slate-800 text-start" :style="{fontSize:size+'px'}">24 دقیقه قبل</span></div>
                </div>
              </div>
            </div>
          </div>
        </div></div>
        <div class="w-3/12"><div>
          <div v-for="items in list2" :key="items.news_id">
            <div v-for="values in items" :key="values.news_id" >
              <div class="m-2.5">
                <div class="w-full"><img :src="`${values.news_image[0].url}`" class="w-full h-28" alt=""></div>
                <div style="width: 100%;max-height: 3rem"><p id="text2" class="font-bold text-black text-sm m-1">{{values.title}}</p></div>
                <div class="flex flex-row align-center w-full justify-start" >
                  <div><span class="font-semibold text-slate-800 text-start" :style="{fontSize:size+'px'}">{{values.visit}}نمایش</span></div>
                  <div><span class="font-semibold text-slate-800 text-start" :style="{fontSize:size+'px'}">24 دقیقه قبل</span></div>
                </div>
              </div>
            </div>
          </div>
        </div></div>
        <div class="w-3/12"><div>
          <div v-for="items in list3" :key="items.news_id">
            <div v-for="values in items" :key="values.news_id" >
              <div class="m-2.5">
                <div class="w-full"><img :src="`${values.news_image[0].url}`" class="w-full h-28" alt=""></div>
                <div style="width: 100%;max-height: 3rem"><p id="text3" class="font-bold text-black text-sm m-1">{{values.title}}</p></div>
                <div class="flex flex-row align-center w-full justify-start" >
                  <div><span class="font-semibold text-slate-800 text-start" :style="{fontSize:size+'px'}">{{values.visit}}نمایش</span></div>
                  <div><span class="font-semibold text-slate-800 text-start" :style="{fontSize:size+'px'}">24 دقیقه قبل</span></div>
                </div>
              </div>
            </div>
          </div>
        </div></div>
        <div class="w-3/12"><div>
          <div v-for="items in list4" :key="items.news_id">
            <div v-for="values in items" :key="values.news_id" >
              <div class="m-2.5">
                <div class="w-full"><img :src="`${values.news_image[0].url}`" class="w-full h-28" alt=""></div>
                <div style="width: 100%;max-height: 3rem"><p id="text4" class="font-bold text-black text-sm m-1">{{values.title}}</p></div>
                <div class="flex flex-row align-center w-full justify-start" >
                  <div><span class="font-semibold text-slate-800 text-start" :style="{fontSize:size+'px'}">{{values.visit}}نمایش</span></div>
                  <div><span class="font-semibold text-slate-800 text-start" :style="{fontSize:size+'px'}">24 دقیقه قبل</span></div>
                </div>
              </div>
            </div>
          </div>
        </div></div>
      </div>
    </div>
  </div>
</section>
</template>

<script>

import{reactive,onMounted,ref} from 'vue'
import EndPoints from "@/service/EndPoints";
import {useRouter} from 'vue-router';
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AllVideo",
  props:{
    slug:{
      type:String,
      required:true,
    }
  },
  setup(){
    const size=ref(13)
    const router=useRouter()
    const list=reactive([])
    const list2=reactive([])
    const list3=reactive([])
    const list4=reactive([])
    const showDetails=onMounted(()=>{
      EndPoints.getAllVideos()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data.news)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    const showInfo2=onMounted(()=>{
      EndPoints.getAllVideosOne()
      .then((response)=>{
        if(response.data.success){
          list2.push(response.data.data.news)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    const resultInfo=onMounted(()=>{
     EndPoints.getAllVideosTwo()
          .then((response)=>{
            if(response.data.success){
              list3.push(response.data.data.news)
            }
          })
          .catch((error)=>{
            handleError(error)
          })
    })
    const total=onMounted(()=>{
      EndPoints.getAllVideosThree()
          .then((response)=>{
            if(response.data.success){
              list4.push(response.data.data.news)
            }
          })
          .catch((error)=>{
            handleError(error)
          })
    })
    return{
      list,
      list2,
      list3,
      list4,
      showDetails,
      router,
      size,
      showInfo2,
      resultInfo,
      total,
    }
  }
}
</script>

<style scoped>
#text,#text2,#text3,#text4{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
</style>